<template>
    <div style="background-color: #ececec; padding: 20px;">
        <a-spin v-if="loading" />
        <a-row v-if="!loading" :gutter="10">
            <a-col v-for="item, i in list" :key="i" :span="4" class="t-left shadow-sm">
                <a-card v-if="i<6" :title="item.name.toLocaleUpperCase() + '/USDT'" :bordered="false">
                    <a-row type="flex" justify="space-around" align="middle" :class="item.class">
                        <a-col :span="12" class="t-big">
                            ${{round(item.priceUsd)}}
                        </a-col>
                        <a-col :span="12" class="t-right">
                            <span v-if="item.changePercent24Hr>0">+{{round(item.changePercent24Hr, 100)}}%</span>
                            <span v-if="item.changePercent24Hr<0">{{round(item.changePercent24Hr, 100)}}%</span>
                            <span v-if="item.changePercent24Hr==0">{{round(item.changePercent24Hr, 100)}}%</span>
                        </a-col>
                    </a-row>
                </a-card>
            </a-col>
        </a-row>
    </div>
</template>

<script>
export default {
    name: "Bg020",
    components: {
    },
    data() {
        return {
            loading: false,
            list: [],
        };
    },
    destroyed: function () {
    },
    methods: {
        round(num, length = 1000000) {
            return Math.round(num * length) / length;
        },
        getClassName(item) {
            if (item.changePercent24Hr > 0) {
                return "up";
            } else if (item.changePercent24Hr < 0) {
                return "down";
            }
            return "none";
        },
        getPercent(item) {
            const resNum = item.close - item.open
            const p1 = resNum / item.close * 100
            return p1
        },
        showListHuobi(a) {
            let t = this;
            let ts = Math.floor(new Date().getTime() / 1000 / 100) * 100;
            a.forEach(function (item) {
                let name = item.name.toLocaleLowerCase();
                item.id = item.name
                item.img = `https://assets.coincap.io/assets/icons/${name}@2x.png`;
                item.img7x24 = `https://images.cryptocompare.com/sparkchart/${name.toLocaleUpperCase()}/USD/latest.png?ts=${ts}`;
                item.priceUsd = item.close
                item.changePercent24Hr = t.getPercent(item)
                item.class = t.getClassName(item);
            });
            this.list = a;
        },
        initDataHuobi() {
            $.get(
                `${this.g.apiUrl}/api/caches?key=huobi-market-tickers`,
                (res) => {
                    this.loading = false;
                    if (res.flag != "ok") {
                        //
                        return;
                    }
                    this.showListHuobi(res.data.value);
                }
            );
        },
        init() {
            const arr = this.g.appWsData
            this.showListHuobi(arr)
            this.$EventBus.$on("appWsComplete", (arr) => {
                this.showListHuobi(arr)
            })
        },
    },
    mounted() {
        this.init();
    },
};
</script>

<style scoped>
.chang24hr {
    border-radius: 5px;
    padding: 5px;
    text-align: center;
}

.up {
    color: #0d8551;
    /* background: #d4f6f0; */
}

.down {
    color: #ef4034;
    /* background: #ffe4e4; */
}

.none {
    color: #999;
    /* background: #eee; */
}
</style>
