<template>
    <div class="container" style="margin-top:60px;">
        <table class="table">
            <thead class="t-gray">
                <td widtd="80">#</td>
                <td widtd="180"></td>
                <td widtd="280"></td>
                <td></td>
                <td></td>
                <td></td>
            </thead>
            <tbody>
                <tr v-for="item in list" :key="item.id" style="font-size:1rem; color:#666;">
                    <td>
                        <img :src="item.img" alt="Bitcoin" width="36px">
                    </td>
                    <td class="t-left">
                        <a-row>{{item.name.toLocaleUpperCase()}}</a-row>
                    </td>
                    <td>${{round(item.priceUsd)}}</td>
                    <td>
                        <div v-if="item.changePercent24Hr>0" :class="item.class" class="chang24hr">+{{round(item.changePercent24Hr)}}</div>
                        <div v-if="item.changePercent24Hr<0" :class="item.class" class="chang24hr">{{round(item.changePercent24Hr)}}</div>
                        <div v-if="item.changePercent24Hr==0" :class="item.class" class="chang24hr">{{round(item.changePercent24Hr)}}</div>
                    </td>
                    <td><img :src="item.img7x24" alt="img"></td>
                    <td>
                        <a-button></a-button>
                    </td>
                </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
export default {
    name: "Bg010",
    components: {
    },
    data() {
        return {
            loading: false,
            list: [],
        };
    },
    methods: {
        round(num, length = 1000000) {
            return Math.round(num * length) / length;
        },
        getClassName(item) {
            if (item.changePercent24Hr > 0) {
                return "up";
            } else if (item.changePercent24Hr < 0) {
                return "down";
            }
            return "none";
        },
        getPercent(item) {
            const resNum = item.close - item.open
            const p1 = resNum / item.close * 100
            return p1
        },
        showListHuobi(a) {
            let t = this
            let ts = Math.floor(new Date().getTime() / 1000 / 100) * 100;
            a.forEach(function (item) {
                let name = item.name.toLocaleLowerCase();
                item.id = item.name
                item.img = `https://assets.coincap.io/assets/icons/${name}@2x.png`;
                item.img7x24 = `https://images.cryptocompare.com/sparkchart/${name.toLocaleUpperCase()}/USD/latest.png?ts=${ts}`;
                item.priceUsd = item.close
                item.changePercent24Hr = t.getPercent(item)
                item.class = t.getClassName(item);
            });
            this.list = a;
        },
        initDataHuobi() {
            this.loading = true;
            $.get(
                `${this.g.apiUrl}/api/caches?key=huobi-market-tickers`,
                (res) => {
                    this.loading = false;
                    if (res.flag != "ok") {
                        //
                        return;
                    }
                    this.showListHuobi(res.data.value)
                }
            );
        },
        init() {
            const arr = this.g.appWsData
            this.showListHuobi(arr)
            this.$EventBus.$on("appWsComplete", (arr) => {
                this.showListHuobi(arr)
            })
        },
    },
    mounted() {
        this.init();
    },
};
</script>

<style scoped>
.chang24hr {
    border-radius: 5px;
    padding: 5px;
    text-align: center;
}

.up {
    background: #d4f6f0;
    color: #0d8551;
}

.down {
    color: #ef4034;
    background: #ffe4e4;
}

.none {
    color: #999;
    background: #eee;
}
</style>
