<script type="text/javascript">
// 
const configKey = function (key) {
    let g = this.config;
    for (let k in g) {
        const item = g[k];
        if (item.key == key) {
            return item.value;
        }
    }
    return null;
};

const round = function (num, intLen = 1, dotLaterLen = 6) {
    let z = "000000000000000000000000000000";
    let zn = Number(1 + z.substr(0, dotLaterLen));
    num = Math.round(Number(num) * zn) / zn;
    let s = String(num);

    if (s.indexOf(".") !== -1) {
        let a = s.split(".");
        let n1 = a[0],
            n2 = a[1];

        if (n1.length < intLen) {
            n1 = z.substr(0, intLen - n1.length) + n1;
        }

        if (n2.length < dotLaterLen) {
            zn = z.substr(0, dotLaterLen - n2.length);
            n2 = String(n2) + zn;
        }

        let res = `${n1}.${n2}`;
        return res;
    }

    let zl = z.substr(0, dotLaterLen);
    return `${s}.${zl}`;
};

const round2 = function (num, len = 2) {
    let z = "000000000000000000000000000000";
    let zr = Number(1 + z.substr(0, len));
    return Math.round(Number(num) * zr) / zr
}

const random = function (min, max) {
    return min + (Math.random() * (max - min))
}

let getQueryVariable = function (variable) {
    // var query = window.location.search.substring(1);
    var query = window.location.hash.substring(1);
    query = query.substring(query.lastIndexOf("?") + 1);
    var vars = query.split("&");
    for (var i = 0; i < vars.length; i++) {
        var pair = vars[i].split("=");
        if (pair[0] == variable) {
            return pair[1];
        }
    }
    return false;
};

let config = {};
let appWsData = null;
let apiUrl = "/";
let webUrl = "/";
let wsUrl = "/";
const container = {
    span: 16,
    offset: 4,
}

let lang = {}

// 
export default {
    // var
    config,
    appWsData,
    apiUrl,
    webUrl,
    wsUrl,
    lang,

    // fn
    configKey,
    round,
    round2,
    random,
    container,
    getQueryVariable,
};
</script>