<template>
    <div class="Head">
        <div class="t-left">
            <a-row type="flex">
                <a-col :span="12">
                    <router-link to="/"><img alt="Vue logo" src="//assets99.oss-cn-hongkong.aliyuncs.com/images/logo/logo-30.png" height="64px" style="margin-right:20px;"></router-link>

                    <router-link to="/">{{this.g.lang.menu_home}}</router-link>
                    <a-divider type="vertical" />
                    <router-link to="/market">{{this.g.lang.menu_market}}</router-link>

                    <a-divider type="vertical" />
                    <router-link to="/futures?symbol=btcusdt">{{this.g.lang.menu_futures}}</router-link>

                    <a-divider type="vertical" />
                    <router-link to="/startup-list">{{this.g.lang.menu_startup}}</router-link>

                </a-col>
                <a-col :span="12" class="t-right">
                    <!--  -->
                    <a-dropdown :trigger="['click']">
                        <a class="ant-dropdown-link" @click="e => e.preventDefault()" style="color:#007bff;">
                            <a-icon type="global" class="icon-style" /> <span>{{this.g.lang.menu_lang}}</span>
                        </a>
                        <a-menu slot="overlay">
                            <a-menu-item v-for="i in lang" :key="i.name" @click="onClickMenuItem(i)">
                                <a href="#"><img :src="i.img" alt="image" height="18"> {{i.name}}</a>
                            </a-menu-item>
                        </a-menu>
                    </a-dropdown>
                    <a-divider type="vertical" />
                    <!--  -->
                    <span v-if="!user_info">
                        <router-link to="/login">{{this.g.lang.menu_login}}</router-link>
                        <a-divider type="vertical" />
                        <router-link to="/register">{{this.g.lang.menu_reg}}</router-link>
                    </span>
                    <!--  -->
                    <span v-if="user_info">
                        <a-dropdown :trigger="['click']">
                            <a class="ant-dropdown-link" @click="e => e.preventDefault()" style="color:#007bff;">
                                <a-icon type="user" class="icon-style" /> <span>{{user_info.account}}</span>
                            </a>
                            <a-menu slot="overlay">
                                <a-menu-item v-for="i in my_menus" :key="i.name">
                                    <router-link :to="i.url">
                                        <span>
                                            {{i.name}}
                                        </span>
                                    </router-link>
                                </a-menu-item>
                            </a-menu>
                        </a-dropdown>
                    </span>

                </a-col>
            </a-row>

        </div>
    </div>
</template>

<script>
export default {
    name: "Head",
    props: {
        msg: String,
    },
    data() {
        return {
            lang: [
                { name: "English", img: '//assets99.oss-cn-hongkong.aliyuncs.com/images/flag/gb.svg', key: 'en' },
                { name: "Franais", img: '//assets99.oss-cn-hongkong.aliyuncs.com/images/flag/fr.svg', key: 'fr' },
                { name: "", img: '//assets99.oss-cn-hongkong.aliyuncs.com/images/flag/kr.svg', key: 'kr' },
                { name: "", img: '//assets99.oss-cn-hongkong.aliyuncs.com/images/flag/jp.svg', key: 'jp' },
                { name: "", img: '//assets99.oss-cn-hongkong.aliyuncs.com/images/flag/hk.svg', key: 'hk' },
            ],
            my_menus: [
                { name: "My Assets", url: "/my/assets" },
                { name: "Contract Account", url: "/my/contract" },
                { name: "Transfer", url: "/my/transfer" },
                { name: "Withdraw", url: "/my/withdraw" },
                { name: "Authentication", url: "/my/authentication" },
                { name: "Login Log", url: "/my/login-log" },
                { name: "Logout", url: "/logout" },
            ],
            user_info: null,
        };
    },
    methods: {
        onClickMenuItem(lang) {
            if (!window.localStorage) {
                return
            }
            window.localStorage["lang"] = lang.key
            window.document.location.reload()
        },
        init() {
            if (window.localStorage) {
                let userInfo = window.localStorage["userInfo"];
                if (userInfo) {
                    this.g.userInfo = JSON.parse(window.atob(userInfo));
                    // console.log(this.g.userInfo.ts, new Date().getTime(), this.g.userInfo.ts < new Date().getTime())
                    if (this.g.userInfo.ts < new Date().getTime()) {
                        window.localStorage.clear();
                        this.$EventBus.$emit("logoutComplete");
                        window.document.location.reload();
                        return
                    }
                    this.user_info = this.g.userInfo;
                }
            }
            this.$EventBus.$on("loginComplete", (userInfo) => {
                this.g.userInfo = userInfo;
                this.user_info = this.g.userInfo;
                if (window.localStorage) {
                    //  24 
                    userInfo.ts = new Date().getTime() + (1000 * 60 * 60 * 24)
                    window.localStorage["userInfo"] = window.btoa(JSON.stringify(userInfo));
                }
            });
            this.$EventBus.$on("logoutComplete", () => {
                this.g.userInfo = null;
                this.user_info = null;
                window.localStorage.clear();
            });
        },
    },
    mounted() {
        this.init();
    },
};
</script>

<style scoped>
.icon-style >>> svg {
    vertical-align: 0em;
}
</style>
