<template>

    <a-config-provider :locale="locale">

        <div v-if="is_show" id="app">
            <a-layout>
                <a-layout-header>

                    <Head />
                </a-layout-header>

                <a-layout-content>
                    <router-view v-if="!loading" />

                    <a-spin v-if="loading" tip="Loading..." class="loading">
                    </a-spin>
                </a-layout-content>

                <a-layout-footer style="background-color: #001529;">
                    <Foot />
                </a-layout-footer>
            </a-layout>

        </div>

    </a-config-provider>

</template>

<script>
import Head from "@/components/Head.vue";
import Foot from "@/components/Foot.vue";
// import zhCN from "ant-design-vue/lib/locale-provider/zh_CN";
import enUS from "ant-design-vue/lib/locale-provider/en_US";

// import moment from 'moment';
// import 'moment/locale/zh-cn';
// moment.locale('en');

export default {
    name: "Home",
    props: {
        msg: String,
    },
    components: {
        Head,
        Foot,
    },
    data() {
        return {
            locale: enUS,
            loading: true,
            is_show: false,

            configTemp: 0,

            ws: null,
            wsTemp: 0,
        };
    },
    destroyed: function () {
        clearTimeout(this.configTemp)
        clearTimeout(this.wsTemp)
    },
    methods: {
        wsReconnect() {
            clearTimeout(this.wsTemp)
            this.wsTemp = setTimeout(() => {
                this.initWS();
            }, 1000);
        },
        initWS() {
            let isShow = true
            this.ws = new WebSocket(`${this.g.wsUrl}/market.tickers`);
            this.ws.onopen = () => {
                console.log("app websocket open ...");
            };
            this.ws.onerror = () => {
                this.wsReconnect()
            }
            this.ws.onclose = () => {
                this.wsReconnect()
            };
            this.ws.onmessage = (event) => {
                this.g.appWsData = JSON.parse(event.data);
                this.$EventBus.$emit("appWsComplete", this.g.appWsData);

                if (isShow) {
                    isShow = false
                    this.is_show = true
                    this.loading = false;
                }
            };
        },
        initLanguage() {
            if (window.localStorage) {
                let langKey = window.localStorage["lang"];
                if (!langKey) {
                    window.localStorage["lang"] = 'en'
                }
                console.log(langKey)
                // this.loading = true;
                const url = `${this.g.apiUrl}/lang/${langKey}.json`
                $.get(url, (res) => {
                    this.g.lang = res
                    // this.is_show = true
                    // this.loading = false;
                    this.initWS()
                }).fail(err => {
                    // this.loading = false;
                    this.$message.error(`Load Error : LanguageUrl ${url}`)
                })
            }
        },

        initConfig() {
            $.get(configUrl, (res) => {
                if (res.flag != "ok") {
                    // this.loading = false;
                    this.$message.error(res.flag);
                    return;
                }
                this.g.config = res.data;
                this.g.apiUrl = this.g.configKey("api_url");
                this.g.webUrl = this.g.configKey("web_url");
                this.g.wsUrl = this.g.configKey("ws_url");
                $("title").text(this.g.configKey("web_name"));

                this.$EventBus.$emit("configComplete", res.data);
                // this.loading = false;

                this.initLanguage()

            }).fail((err) => {
                this.$message.error(`Load Error : ConfigUrl ${configUrl}`)
                clearTimeout(this.configTemp)
                this.configTemp = setTimeout(() => {
                    this.initConfig()
                }, 5000)
            });
        },

        init() {
            this.loading = true;
            this.initConfig()
        },
    },
    mounted() {
        this.init();
    },
};
</script>

